html {
  background-color: #0d2143;

}

.App {
  text-align: center;
  color: white;
}
.login-form{
  display: block;
  margin: auto;
  width: 400px;
}
.login-email{
  display: block;
  margin: auto;

}
.login-password{
  display: block;
  margin: auto;

}

/* header */

.header--main {
  display: flex;
  justify-content: space-between;
  margin: 5vh 5vw;
}
.menu--main {
  height: auto;
}
.img--prozeta-logo {
  display:block;
  height: 50px;
}
.wrapper--profile-picture{
  margin-right: 1.5rem;

}

.img--profile-picture {
  height: 2.8rem;
  border-radius: 50%;
  border: solid 0.12rem white;
}
.wrapper--profile-picture, .wrapper--login-button, .wrapper--logout-button {
  display: inline-block;
}

.wrapper--login-button, .wrapper--logout-button{
  height:3rem;
  vertical-align: top;
}
.login-button, .logout-button {
  display: block;
  position: relative;
  margin-top: 0.2rem;
  padding: 0.8rem 1.2rem;
  border: none;
  border-radius: 1px;
  background-color: #3dbc9f;
  color:white;
  font-family: "Arimo", sans-serif;
  font-size: 1rem;
  font-weight: 500;
  letter-spacing: 1px;
  text-transform: uppercase;
  cursor: pointer;

}

/* profile */
.user-info {
  height: 100%;
}

.user-info--wrapper {

  max-width: 500px; /* když by měl někdo delší email než 500 - šířka rámečku? */
  border: solid 7px #3dbc9f;
  border-radius: 5px;
  margin: 10vh auto 2vh auto;
  padding: 20px;
  text-align: left;
}

.profile--profile-picture {
 float: left;
  height: 100px;
  margin-right: 2rem;
}
.profile--header {
  display: inline-block;
  min-height: 100px;
}

.profile--list {
  list-style-type: none;
  padding-left: 0px;
}

.profile--list li span {
  font-weight: 600;
}

/*api*/
.api-list {
  margin-top: 15vh;
}